import showMessage from './message';

const buyProducts = (money, totalPrice, textForBuy, textForNotBuy) =>{
  if (money > totalPrice){
    showMessage(
      'check',
      `${textForBuy}`,
      'fa-check-circle'
    );
  } else {
    showMessage(
      'danger',
      `${textForNotBuy}`,
      'fa-window-close'
    );
  }
}

export default buyProducts;