import Swal from 'sweetalert2';

export default function showMessage(id, message, className){
  Swal.fire({
    showConfirmButton: false,
    padding: '10px 0',
    background: 'rgba(255,255,255,0.8)',
    html:
    `<span id="${id}" class="alert">${message} <i class="fas ${className}"></i></span>`
  })
}